.page {
	position: fixed;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	will-change: opacity;
	-webkit-transition: all 1000ms ease;
	-o-transition: all 1000ms ease;
	transition: all 1000ms ease;
}

.page.transition-enter {
	opacity: 0;
	-webkit-transition-delay: 2000ms;
	     -o-transition-delay: 2000ms;
	        transition-delay: 2000ms;
}

.page.transition-enter-active {
	opacity: 1;
	pointer-events: none;
}

.page.transition-exit {
	opacity: 1;
	pointer-events: none;
	-webkit-transition-delay: 0ms;
	     -o-transition-delay: 0ms;
	        transition-delay: 0ms;
}

.page.transition-exit-active {
	opacity: 0;
}

p,
span {
	font-family: Akkurat-Mono, helvetica, arial, sans-serif;
}

.cubic,
.cubic::before,
.cubic::after {
	-webkit-transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
	     -o-transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
	        transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
}

.quint,
.quint::before,
.quint::after {
	-webkit-transition-timing-function: cubic-bezier(1, 0, 0, 1);
	     -o-transition-timing-function: cubic-bezier(1, 0, 0, 1);
	        transition-timing-function: cubic-bezier(1, 0, 0, 1);
}

h1,
h2,
h3,
h4,
p,
span,
pre,
section {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

h1,
h2,
h3,
h4 {
	font-family: 'freesansbold', 'helvetica', 'arial', 'sans-serif';
}

h1,
h2 {
	color: #9c153f;
	margin-left: -0.06em;
}

h3,
h4 {
	color: #000;
}

h1 {
	line-height: calc(100vmin / 24 * 2.8);
	font-size: calc(100vmin / 24 * 5.65);
	letter-spacing: -1vmin;
}

h2 {
	line-height: calc(100vmin / 24 * 2);
	font-size: calc(100vmin / 24 * 3);
	letter-spacing: -0.5vmin;
}

h3 {
	line-height: calc(100vmin / 24 * 2);
	font-size: calc(100vmin / 24 * 2);
	letter-spacing: -0.7vmin;
	border-bottom: 3px solid black;
}

h4 {
	line-height: calc(100vmin / 24 * 0.5);
	font-size: calc(100vmin / 24 * 0.5);
	letter-spacing: -0.09vmin;
}

p {
	font-family: freesansoblique;
	font-size: 0.8vmin;
}

img {
	-o-object-fit: cover;
	   object-fit: cover;
	height: 100%;
	width: 100%;
}

pre {
	height: 100%;
	width: 100%;
	display: block;
	white-space: pre-line;
}

span {
	font-family: Akkurat-Mono, monospace;
	text-transform: uppercase;
	line-height: 1.9vmin;
	font-size: 1.16vmin;
	letter-spacing: 0.03vmin;
}



