.main-page {
	display: grid;
	height: 100vh;
	width: 100vw;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-line-pack: center;
	    align-content: center;
	grid-template-columns: repeat(24, calc(100vmin / 24));
	grid-template-rows: repeat(24, calc(100vmin / 24));
}

.main-page.transition-enter .animate--top {
	-webkit-transform: translateY(-100vh);
	    -ms-transform: translateY(-100vh);
	        transform: translateY(-100vh);
}
.main-page.transition-enter .animate--left {
	-webkit-transform: translateX(-100vw);
	    -ms-transform: translateX(-100vw);
	        transform: translateX(-100vw);
}
.main-page.transition-enter .animate--right {
	-webkit-transform: translateX(100vw);
	    -ms-transform: translateX(100vw);
	        transform: translateX(100vw);
}
.main-page.transition-enter .animate--bottom {
	-webkit-transform: translateY(100vh);
	    -ms-transform: translateY(100vh);
	        transform: translateY(100vh);
}

.main-page.main-page.transition-enter .animate--top,
.main-page.main-page.transition-enter .animate--left,
.main-page.main-page.transition-enter .animate--right,
.main-page.main-page.transition-enter .animate--bottom {
	-webkit-transition-timing-function: 0;
	     -o-transition-timing-function: 0;
	        transition-timing-function: 0;
}

.main-page.transition-enter-done .animate--top,
.main-page.transition-enter-done .animate--left,
.main-page.transition-enter-done .animate--right,
.main-page.transition-enter-done .animate--bottom {
	-webkit-transform: 0;
	    -ms-transform: 0;
	        transform: 0;
}



