.landing-page {
	display: -ms-flexbox;
	display: flex;
}

.landing-page__button {
	position: relative;
	margin: auto;
	font-size: calc(36px - 2vmin);
	line-height: 1em;
	text-transform: uppercase;
	will-change: opacity;
	opacity: 0.75;
	letter-spacing: 1px;
	cursor: pointer;
}

.landing-page__word {
	font-size: inherit;
	display: inline-block;
	-webkit-transition: all 800ms;
	-o-transition: all 800ms;
	transition: all 800ms;
	line-height: inherit;
	padding: 0 5px;
}

.landing-page__word:nth-of-type(1) {
	-webkit-transition-delay: 300ms;
	     -o-transition-delay: 300ms;
	        transition-delay: 300ms;
	-webkit-transition-duration: 500ms;
	     -o-transition-duration: 500ms;
	        transition-duration: 500ms;
}

.landing-page__word:nth-of-type(2) {
	-webkit-transition-delay: 200ms;
	     -o-transition-delay: 200ms;
	        transition-delay: 200ms;
	-webkit-transition-duration: 600ms;
	     -o-transition-duration: 600ms;
	        transition-duration: 600ms;
}

.landing-page__word:nth-of-type(3) {
	-webkit-transition-delay: 100ms;
	     -o-transition-delay: 100ms;
	        transition-delay: 100ms;
	-webkit-transition-duration: 700ms;
	     -o-transition-duration: 700ms;
	        transition-duration: 700ms;
}

.landing-page__word:nth-of-type(4) {
	-webkit-transition-delay: 0ms;
	     -o-transition-delay: 0ms;
	        transition-delay: 0ms;
	-webkit-transition-duration: 800ms;
	     -o-transition-duration: 800ms;
	        transition-duration: 800ms;
}

.landing-page__button:hover .landing-page__word:nth-of-type(1) {
	-webkit-transition-delay: 0ms;
	     -o-transition-delay: 0ms;
	        transition-delay: 0ms;
	-webkit-transition-duration: 800ms;
	     -o-transition-duration: 800ms;
	        transition-duration: 800ms;
}

.landing-page__button:hover .landing-page__word:nth-of-type(2) {
	-webkit-transition-delay: 100ms;
	     -o-transition-delay: 100ms;
	        transition-delay: 100ms;
	-webkit-transition-duration: 700ms;
	     -o-transition-duration: 700ms;
	        transition-duration: 700ms;
}

.landing-page__button:hover .landing-page__word:nth-of-type(3) {
	-webkit-transition-delay: 200ms;
	     -o-transition-delay: 200ms;
	        transition-delay: 200ms;
	-webkit-transition-duration: 600ms;
	     -o-transition-duration: 600ms;
	        transition-duration: 600ms;
}

.landing-page__button:hover .landing-page__word:nth-of-type(4) {
	-webkit-transition-delay: 300ms;
	     -o-transition-delay: 300ms;
	        transition-delay: 300ms;
	-webkit-transition-duration: 500ms;
	     -o-transition-duration: 500ms;
	        transition-duration: 500ms;
}

.landing-page__button::after {
	height: 2px;
	width: 0;
	background-color: #9c153e;
	position: absolute;
	bottom: 0;
	left: 0%;
	will-change: width;
	content: '';
	-webkit-transition: width 800ms;
	-o-transition: width 800ms;
	transition: width 800ms;
}

.landing-page__button:hover {
	opacity: 1;
}

.landing-page__button:hover .landing-page__word {
	-webkit-transform: translateY(-2vmin);
	    -ms-transform: translateY(-2vmin);
	        transform: translateY(-2vmin);
}

.landing-page__button:hover::after {
	width: 100%;
}

.landing-page.transition-exit > .landing-page__button::after {
	right: 0;
	left: initial;
}

.landing-page.transition-exit > .landing-page__button > .landing-page__word {
	-webkit-transform: translateY(-8vmin);
	    -ms-transform: translateY(-8vmin);
	        transform: translateY(-8vmin);
}



