* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	overflow: hidden;
}

@font-face {
	font-family: 'freesansmedium';
	src: url('./Media/Fonts/freesans-webfont.woff2') format('woff2'),
		url('./Media/Fonts/freesans-webfont.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

