		url('./Media/Fonts/freesansoblique-webfont.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Akkurat-Mono';
	src: url('./Media/Fonts/Akkurat-Mono.woff2') format('woff2'),
